// @flow
import React, { useState, Fragment } from 'react';
import type { Element } from 'react';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import _get from 'lodash/get';
import { APP_BASE_URL, OFFER_STEPS } from 'shared/Consts';
import type { Offer } from 'types/Offer';

type Props = {
  offer: Offer,
};

const ActionButton = ({ offer }: Props): Element<typeof Fragment> => {
  const [anchorEl, setAnchorEl] = useState(null);
  const offerId = _get(offer, ['OfferId']);
  const formId = _get(offer, ['Forms', 0, 'FormId']);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const redirectUrl = `${APP_BASE_URL}/offer?offerId=${offerId}&step=${OFFER_STEPS.pleaseComplete}&formId=${formId}`;

  return (
    <>
      <button
        type="button"
        className="minimal-btn animated_fastest"
        onClick={handleClick}
      >
        <i className="fa fa-ellipsis-h"></i>
      </button>
      <Menu
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <MenuItem component="a" href={redirectUrl}>
          Continue Offer
        </MenuItem>
      </Menu>
    </>
  );
};

export default ActionButton;
